import {
	Component,
	OnInit
} from '@angular/core';
import {
	MenuService,
	UsersService
} from 'src/app/service/index.service';

@Component({
	selector: 'app-sidebar',
	templateUrl: './sidebar.component.html',
	styles: []
})
export class SidebarComponent implements OnInit {
	menu = [];
	constructor(public _menuService: MenuService, private _userService: UsersService) {}

	ngOnInit() {
		if (this._userService.UserData.role == 'admin') {
			this.menu = this._menuService.menu;
		} else if (this._userService.UserData.role == 'teacher') {
			// console.log(this._userService.UserData.role);
			this.menu = this._menuService.menu1;
		}
	}
}
