import {
  Component,
  OnInit,
  EventEmitter,
  OnDestroy
} from '@angular/core';
import {
  Router,
  ActivatedRoute
} from '@angular/router';
import {
  CoursesService,
  MenuService,
  ViewsService,
  ViewsCoursesService,
  UsersService,
  ModalUploadService
} from 'src/app/service/index.service';
import {
  Courses
} from 'src/app/models/course.model';
declare function scrollInicio();
declare function initCourse();

@Component({
  selector: 'app-view-courses',
  templateUrl: './view-courses.component.html',
  styles: []
})
export class ViewCoursesComponent implements OnInit, OnDestroy {
  value = 4;
  textButton = 'Update';
  starList: string[] = [];

  constructor(
	public router: Router,
	public _OneCourse: CoursesService,
	public activateRouter: ActivatedRoute,
	private _menu: MenuService,
	private _modalService: ViewsService,
	private _modalViewCourse: ViewsCoursesService,
	private _userSevice: UsersService,
	private _modalUpload: ModalUploadService
  ) {
	
	this.activateRouter.params.subscribe((params: any) => {
	  if (params.id == 'nuevo') {
	this._menu.resetModelCourse();
	this._modalViewCourse.mostrarModal();
	this._menu.modelCourse.teacher[0].name = this._userSevice.UserData.name
	this._modalViewCourse.showCategory.emit(1);
	this.textButton = 'Save';
	this._modalViewCourse.off = false;
	this._modalViewCourse.active.name = true;

	  }
	  if (params.id != 'nuevo') {
		  this._modalViewCourse.resetElement();
		  this._modalViewCourse.active.name = false;
		  this._OneCourse.showOne(params.id).subscribe((resp: any) => {
			  this._menu.modelCourse = resp.data
			  this._modalViewCourse.off = true;
			});
			

	  }
	});
  }

  ngOnInit() {

	for (let i = 1; i <= 5; i++) {
	  if (i <= this.value) {
	this.starList.push('fas fa-star filled');
	  } else if (i <= this.value + 0.5) {
	this.starList.push('fas fa-star-half filled');
	  } else {
	this.starList.push('fas fa-star');
	  }
	}
	initCourse();
	scrollInicio()
  }

  butonSave() {
	if (this.textButton == 'Update') {
	  return this.textButton = 'Save';
	} else {
	  this.textButton = 'Update';
	}
  }
  mostrarModal(value) {
	this._modalViewCourse.mostrarModal(value);
  }
  changerImg() {
	this._modalService.cerraModal();
	this._modalViewCourse.cerraModal();
	this._modalUpload.nimImg = 'courses';
	this._modalUpload.modtrarModal('courses-img', this._menu.modelCourse.id, this._menu.modelCourse.img);
  }
  ngOnDestroy() {
	  this._modalViewCourse.cerraModal();
	  this._menu.resetModelCourse();
  }	
}