import { Component, OnInit } from '@angular/core';
import { MenuService, UsersService, LockScreenService } from 'src/app/service/index.service';
import { Router } from '@angular/router';
import { UserModel } from 'src/app/models/user.model';
declare function initLoader();
@Component({
	selector: 'app-init-pages',
	templateUrl: './init-pages.component.html',
	styles: []
})
export class InitPagesComponent implements OnInit {
	nameUser = '..';
	Users: UserModel;
	searchInit: string;
	oculto = true;
	constructor(
		public _userService: UsersService,
		public _menu: MenuService,
		private _modalServidce: LockScreenService,
		public router: Router) { }


	ngOnInit() {
		initLoader();
		this._menu.removePreload();
		this.setIntervalSession().then(resp => {
			if ( resp ) {
				this._modalServidce.mostrar();
			}
		});
	}
	searchEmiter(value) {
		if (value.length == 0) {
			this.router.navigate(['/dashboard']);
		} else {
			this.router.navigate(['/search', value]);
		}
	}
	setIntervalSession() {
		let contador = 0;
		return new Promise((resolve, reject) => {
			setInterval(() => {
				contador += 1;
				if(contador === 1000) {
					resolve(true);
				}
			}, 1000);
		});
	}

}
