import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { Category } from '../../models/category.model';
import { Router } from '@angular/router';
import { CoursesService, CategoryService, UsersService, MenuService, ModalMapsService } from 'src/app/service/index.service';
import { UserModel } from 'src/app/models/user.model';

@Component({
	selector: 'app-courses',
	templateUrl: './courses.component.html',
	styles: []
})
export class CoursesComponent implements OnInit {
	rows_Course = [];
	loading: boolean = true;
	isChecked = true;
	Total_lesson = [];
	Total_Enrolments = [];
	category: Category[] = [];
	users: UserModel[] = [];
	Students: UserModel[] = [];
	arrayFilter = { category: '', teacher: '', status: '', name: '', price: '' };
	columns = [];
	desde: number = 1;
	nextPege: number;
	totalRegister: number;
	next_page: any;
	prev_page: any;
	current_page: any;
	total_pages: any;


/* _______________________________ */
	activeLessons = false;
	activeSections = false;
	activePeriod = false;
	activeStatus = false;
	activeTeacher = false;
	activeCategory = false;
	activePrice = false;
	activeEnrolments = false;
	activePublished = false;

/* _______________________________ */
	ColumnStatus = true;
	ColumnName = true;
	ColumnAuthor = true;
	ColumnCategory = true;
	ColumnPrice = true;
	ColumnLessons = true;
	ColumnSections = true;
	ColumnDemography = true;
	ColumnEnrolments = true;
	ColumnPublished = true;
	ColumnRevenue = true;
	ColumnsubCategory = false;
/* _______________________________ */


	array = { name: '' };
	allFilter = true;
	oculFilter = 1;
	constructor(
		public _coursesServices: CoursesService,
		public _userService: UsersService,
		public _categoryService: CategoryService,
		public router: Router,
		public _modalMap: MenuService,
		public _modalMaps: ModalMapsService
	) { }

	ngOnInit() {
		this.ListCourses();
		this._categoryService.listCategory(this.array).subscribe((resp: any) => {
			this.category = resp.data;
		});
		this._userService.cargarUsuarios().subscribe((resp: any) => {
			this.users = resp.data;
		});
	}

	updateFilter(event, type) {
		let val = event.toLowerCase();
		if (type == 'category') {
			this.arrayFilter.category = val;
		}
		if (type == 'status') {
			this.arrayFilter.status = val;
		}
		if (type == 'teacher') {
			this.arrayFilter.teacher = val;
		}
		if (type == 'price') {
			this.arrayFilter.price = val;
		}
		if (type == 'name') {
			this.arrayFilter.name = val;
		}
		if (type == 'ocultoAll') {
			console.log(val);

		}
		this.ListCourses();
	}
	nuevooculto(event) {
		this.activeLessons = event
		this.activeSections = event
		this.activePeriod = event
		this.activeStatus = event
		this.activeTeacher = event
		this.activeCategory = event
		this.activePrice = event
		this.activeEnrolments = event
		this.activePublished = event
	}

	deleteCourse(value) {
		this.loading = true;
		this._coursesServices.deleteCourse(value).subscribe(resp => {
			this.ListCourses();
			this.loading = false;
		});
	}

	ListCourses() {
		this.loading = true;
		this._coursesServices.listCourses(this.desde, this.arrayFilter).subscribe((resp: any) => {

			this.rows_Course = resp.data;

			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page;
			this.total_pages = resp.last_page;
			this.loading = false;
		})
	}



	views() {
		this._modalMap.mostraModal();
	}
	cambiarDesde(value: number) {

		this.desde += value;
		if (this.desde < 1) {
			this.desde = 1;
		}
		this.ListCourses();

	}
	GenerarAchivo(type) {
		if (type == 'PDF') {
			this._coursesServices.showPDF();
		}
		if (type == 'EXCEL') {
			this._coursesServices.showExcel();
		}
	}
	MostrarMaps(lat, lng, text) {
		this._modalMaps.lat = lat;
		this._modalMaps.lng = lng;
		this._modalMaps.texto = text;
		console.log(text);

		this._modalMaps.cargarModal();
	}
}