import { Component, OnInit } from '@angular/core';
import { UsersService, ModalUploadService } from 'src/app/service/index.service';
import { UserModel } from 'src/app/models/user.model';

@Component({
	selector: 'app-myprofile',
	templateUrl: './myprofile.component.html',
	styles: []
})
export class MyprofileComponent implements OnInit {
	User = { name: '' };
	constructor(
		public _userService: UsersService,
		public _modalService: ModalUploadService
	) {

		if (this._userService.UserData.role == 'teacher') {
			this._userService.showOneTeacher(this._userService.UserData.id).subscribe((resp: any) => {
				this.User = resp.data;
				console.log(this.User);
			});
		}
	}

	ngOnInit() {
	}
	updateUser(value) {
		const user = new UserModel(
			value.name,
			value.lastname,
			this._userService.UserData.email,
			null,
			null,
			null,
			value.Biography,
			null,
			null,
			null,
			null,
			null,
			null,
			null,
			null,
			this._userService.UserData.role,
			null,
			null,
			value.Company,
			value.Address,
			value.City,
			value.Country,
			value.Postal_Code,
			null,
			null
		);
		this._userService.update(user, this._userService.UserData.id).subscribe((resp: any) => {
			this._userService.guardarStorage(resp.data, 'kadasdasddiasid');
		});
	}
}
