import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UsersService, SubCategoryService } from 'src/app/service/index.service';

@Component({
	selector: 'app-search',
	templateUrl: './search.component.html',
	styles: []
})
export class SearchComponent implements OnInit {
	termino: string;
	Category: any = [];
	Student: any = [];
	Instructor: any = [];
	Course: any = [];
	subCategory: any = [];
	constructor(
		public _searService: UsersService,
		public activationRouter: ActivatedRoute,
		public _subCategory: SubCategoryService
	) { }

	ngOnInit() {
		this.activationRouter.params.subscribe(resp => {
			this.termino = resp['termino'];
			this.cargar();
		});
		this._subCategory.eventEdit.subscribe((resp) => {
			this.cargar();
		})
	}
	editSubCategory(id, parent) {
		this._subCategory.mostrarModall(id, parent);
	  }
	  cargar() {
		this._searService.searchAll(this.termino).subscribe((respData: any) => {
			this.Course = respData.data.course;
			this.Category = respData.data.category;
			this.Student = respData.data.student;
			this.Instructor = respData.data.teacher;
			this.subCategory = respData.data.subcategory;
		});
	  }
}
