import { Component, OnInit } from '@angular/core';
import { MenuService, UsersService, ModalUploadService } from 'src/app/service/index.service';
import { Subscription } from 'rxjs';
import { UserModel } from 'src/app/models/user.model';
import { ToastrService } from 'ngx-toastr';

@Component({
	selector: 'app-users',
	templateUrl: './users.component.html',
	styles: []
})
export class UsersComponent implements OnInit {
	teacher: Subscription;
	student: Subscription;
	getData: any = [];
	user: UserModel[] = [];
	loading = true;
	arrayFilter = { name: '', lastname: '', verified: '' };
	removeFiltes = false;
	// -----------------------------


	activeName = false;
	activeEmail = false;
	activeCreated = false;
	activeState = false;
	activeCompany = false;
	activeAddress = false;
	activeCity = false;
	activeCountry = false;
	activePostal_Code = false;
	activePhone = false;
	activeIP_Address = false;
	// -----------------------------
	ColumnName = true;
	ColumnEmail = true;
	ColumnCreated = true;
	ColumnState = true;
	ColumnCompany = true;
	ColumnAddress = true;
	ColumnCity = true;
	ColumnCountry = true;
	ColumnPostal_Code = true;
	ColumnPhone = true;
	ColumnIP_Address = true;
	desdeStudent = 1;
	desdeInstructor = 1;
	// ____________________________________________
	// tslint:disable-next-line: variable-name
	current_page: number;
	total_pages: number;
	next_page: number;
	prev_page: number;
	totalRegister: number;
	nextPege: number;

	constructor(
		public _headerService: MenuService,
		public _userService: UsersService,
		public _modalService: ModalUploadService,
		public alert: ToastrService
	) {
		_headerService.getDataRouter()
			.subscribe((resp: any) => {
				this.getData = resp;
				if (resp.sub === 'teacher') {
					this.user = null;
					this.showTeacher();
				}
				if (resp.sub === 'studen') {
					this.user = null;
					this.showStudent();
				}
			});
	}
	ngOnInit() {
		this._modalService.deleteNotification.subscribe(esp => {
			if (this.getData.title == 'Instructors') {
				this.showTeacher();
			} else if (this.getData.title == 'Students') {
				this.showStudent();
			}
		});
	}

	showTeacher() {
		this.loading = true;
		this._userService.allTeacher(this.desdeInstructor, this.arrayFilter).subscribe((resp: any) => {
			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page;
			this.total_pages = resp.last_page;

			this.user = resp.data;
			console.log(resp.data);

			// console.log(this._headerService.converFecha(resp.data.));

			this.loading = false;
		});
	}

	showStudent() {
		this.loading = true;
		this._userService.allStudents(this.desdeStudent, this.arrayFilter).subscribe((resp: any) => {
			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page;
			this.total_pages = resp.last_page;
			this.user = resp.data;
			this.loading = false;
		});
	}

	deleteUser(id) {

		if (this._userService.UserData.id == id) {
			this.alert.info('cannot be removed', 'It`s not valid');
			return;
		}
		this._modalService.mostraModalUser(id, this.getData.title);
	}
	funtionDesde(value) {
		if (this.getData.title == 'Students') {
			if (this.desdeStudent < 1) {
				return this.desdeStudent = 1
			}
			this.desdeStudent += value;
			this.showStudent();
		} else if (this.getData.title == 'Instructors') {
			if (this.desdeInstructor < 1) {
				return this.desdeInstructor = 1;
			}
			this.desdeInstructor += value;
			this.showTeacher();
		}
	}

	updateFilterStudemt(event, type) {
		const val = event.toLowerCase();
		if (type == 'name') {
			this.arrayFilter.name = val;
			this.arrayFilter.lastname = val;
		}
		if (type == 'verified') {
			this.arrayFilter.verified = val;
		}
		this.showStudent();

	}
	updateFilterTeacher(event, type) {
		const val = event.toLowerCase();
		if (type == 'name') {
			this.arrayFilter.name = val;
		}
		if (type == 'lastname') {
			this.arrayFilter.lastname = val;
		}
		if (type == 'verified') {
			this.arrayFilter.verified = val;
		}
		this.showTeacher();
	}
	nuevooculto(value) {

		this.activeName = value;
		this.activeEmail = value;
		this.activeCreated = value;
		this.activeState = value;
		this.activeCompany = value;
		this.activeAddress = value;
		this.activeCity = value;
		this.activeCountry = value;
		this.activePostal_Code = value;
		this.activePhone = value;
		this.activeIP_Address = value;
	}
}
