import { Component, OnInit } from '@angular/core';
import { MenuService, UsersService } from 'src/app/service/index.service';
import { ToastrService } from 'ngx-toastr';
import { UserModel } from 'src/app/models/user.model';
import { NgForm } from '@angular/forms';
@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styles: []
})
export class LoginComponent implements OnInit {

	event = document.createEvent('UIEvents');

	recuerdame = false;
	email: string;
	emailValidate: string;
	constructor(
		public alert: ToastrService,
		private _serviceUser: UsersService
	) { }

	ngOnInit() {
		document.getElementById('styleApp').remove();
		this.email = localStorage.getItem('email') || '';
		this.emailValidate = this.email;
		if (this.email.length > 1) {
			this.recuerdame = true;
		}
	}
	loginSession(forma: NgForm) {

		if (forma.invalid) {
			return;
		}

		const user = new UserModel(null, null, forma.value.email, forma.value.password, null, null);
		this._serviceUser.initSession(user, forma.value.condicions).subscribe(resp => {
			if (resp == true) {
				window.location.href = '/';
			} else {
				this.alert.error('Your email and password does not match. Please, verify and try again.', 'Login Failed');
			}

		});
	}
}
