import { Injectable, EventEmitter } from '@angular/core';
import { Category } from '../../models/category.model';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { URL_SERVICES } from '../../config/config';
import { ToastrService } from 'ngx-toastr';
import { catchError, map } from 'rxjs/operators';
import { throwError } from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class CategoryService {
	public eventUpdateCategory = new EventEmitter<any>();
	public categoryEvent = new EventEmitter<any>();
	 
	constructor(
		public http: HttpClient,
		public router: Router,
		public alert: ToastrService
	) { }
	addCategoty(newCategory: Category) {

		const url = `${URL_SERVICES}/categories`;
		return this.http.post(url, newCategory).pipe(
			catchError(erro => {
				const er = erro.error.error;
				if (er.name == 'The name has already been taken.') {
					this.alert.error('The name already exists.', 'Error')
				}
				// if (er.name) {
				//   return this.alert.info('The name is necessary');
				// }
				if (er.codecategory) {
					this.alert.error('el codecategory es necesario');
					// swal('error', '', 'error');
				}
				if (er.description) {
					this.alert.error('el codecategory es necesario');


				}

				return throwError(erro);
			})
		);
	}

	listCategory(array) {

		const url = `${URL_SERVICES}/categories?name=${array.name}`;
		return this.http.get(url).pipe(
			catchError(e => {
				console.log('Error listCategory', e);
				return throwError(e);
			}),
			map((resp: any) => {
				// console.log(resp.data);

				return resp;
			})
		);
	}

	deleteCategory(id) {
		const url = `${URL_SERVICES}/categories/${id}`;
		return this.http.delete(url).pipe(
			catchError((err: any) => {
				console.log('deleteCategory', err);
				return throwError(err);
			})
		);
	}

	editCategory(id, category: Category) {
		const url = `${URL_SERVICES}/categories/${id}`;
		return this.http.put(url, category).pipe(
			catchError((err: any) => {
				console.log('editCategory', err);
				return throwError(err);
			})
		);

	}
	showOne(id) {
		const url = `${URL_SERVICES}/categories/${id}`;
		return this.http.get(url, id).pipe(
			catchError(err => {
				console.log(err);
				return throwError(err);
			})
		);

	}
}
