import { Injectable, EventEmitter } from '@angular/core';

@Injectable({
	providedIn: 'root'
})
export class ModalUploadService {
	public tipo: string;
	public id: string;
	public oculto = 'oculto';
	public oculdeteUser = 'oculto';
	public nimImg = ''
	public previewsImg:string;
	public notificacion = new EventEmitter<any>();
	public deleteNotification = new EventEmitter<any>();
	constructor() { }

	OcultarModal() {
		this.oculto = 'oculto';
		this.oculdeteUser = 'oculto';
		this.id = null;
		this.tipo = null;
	}
	modtrarModal(type, id, previewImg = 'null') {
		this.previewsImg = previewImg;
		this.oculto = '';
		this.id = id;
		this.tipo = type;
		console.log(id);

	}
	mostraModalUser(id, typo) {
		
		this.oculdeteUser = '';
		this.id = id;
		this.tipo = typo;
	}
}
