import { Component, OnInit} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { UserModel } from 'src/app/models/user.model';
import { UsersService, ModalUploadService, UploadService, MenuService } from 'src/app/service/index.service';
import { ToastrService } from 'ngx-toastr';

@Component({
	selector: 'app-modal-upload',
	templateUrl: './modal-upload.component.html',
	styles: []
})
export class ModalUploadComponent implements OnInit {
	imagenSubir: File;
	forma: FormGroup;
	imagenTemporal: string | ArrayBuffer;
	Users: UserModel;
	constructor(
		public _userService: UsersService,
		public _Modalservicas: ModalUploadService,
		public _archivoService: UploadService,
		private alert: ToastrService,
		private _courseModel: MenuService
	) { }

	ngOnInit() {
	}


	ocultarModal() {
		this.imagenSubir = null;
		this.imagenTemporal = null;
		this._Modalservicas.OcultarModal();
	}


	modalimagen(event: File) {
		if (!event) {
			this.imagenSubir = null;
			return;
		}

		this._archivoService.selectImg(event);
	}
	subirImgen() {
		this._archivoService.sublirArchivo(this._Modalservicas.tipo, this._Modalservicas.id)
			.then((resp: any) => {
				console.log(resp);
				if(this._Modalservicas.tipo == 'category') {

				}
				if(this._Modalservicas.tipo == 'users') {
					// this.nimImg = 'courses';
					this._userService.guardarStorage(resp.data, 'jnajsnd');
					this._userService.UserData = resp.data;
				}
				if(this._Modalservicas.tipo == 'courses-img') {
					// this.nimImg = 'courses';
					this._courseModel.modelCourse.img = resp.data.img
				}
				if(this._Modalservicas.tipo == 'courses-img') {
					// this.nimImg = 'courses';
					
				}
				this._Modalservicas.OcultarModal();
				
				
				this.alert.success('update', 'updated image');

			})
			.catch(err => console.log(err));
	}
	delete() {
		if (this._Modalservicas.tipo == 'Instructors') {
			// this._
			this._userService.Destroy(this._Modalservicas.id).subscribe(resp => {
				this._Modalservicas.deleteNotification.emit(resp);
				this._Modalservicas.OcultarModal();
			})
		}
		if (this._Modalservicas.tipo == 'Students') {

			this._userService.Destroy(this._Modalservicas.id).subscribe(resp => {
				this.alert.success('Students Deleted', 'with success');
				this._Modalservicas.deleteNotification.emit(resp);
				this._Modalservicas.OcultarModal();
			})

		}
	}
}
