import { RouterModule, Routes } from '@angular/router';

import { SearchComponent } from './search/search.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CoursesComponent } from './courses/courses.component';
import { InitPagesComponent } from './init-pages/init-pages.component';
import { CategoryComponent } from './category/category.component';
import { AddCatgoryComponent } from './category/add-catgory.component';
import { MyprofileComponent } from './myprofile/myprofile.component';
import { AccountSettingComponent } from './account-setting/account-setting.component';
import { EnrolmentComponent } from './enrolment/enrolment.component';
import { ReportComponent } from './report/report.component';
import { MessageComponent } from './message/message.component';
import { AddUsersComponent } from './users/add-users.component';
import { UsersComponent } from './users/users.component';
import { LoginGuard } from '../Guard/login.guard';
import { AddCoursesComponent } from './courses/add-courses.component';



const pagesRoutes: Routes = [
	{
		path: '', component: InitPagesComponent,
		canActivate: [LoginGuard],
		children: [
			{ path: 'dashboard', component: DashboardComponent, data: { title: 'Dashboard' } },
			{ path: 'courses', component: CoursesComponent, data: { title: 'Courses' } },
			{ path: 'category', component: CategoryComponent, data: { title: 'categories' } },
			{ path: 'addcategory/:id', component: AddCatgoryComponent, data: { title: 'new Category' } },
			{ path: 'profile', component: MyprofileComponent, data: { title: 'My Profile' } },
			{ path: 'account-setting', component: AccountSettingComponent, data: { title: 'Account setting' } },
			// { path: 'privacy-setting', component: PrivacySettingComponent, data: { title: 'Privacy setting' } },
			{ path: 'addCourses/:id', component: AddCoursesComponent, data: { title: 'addCourse' } },
			{ path: 'enrolment', component: EnrolmentComponent, data: { title: 'Enrolment' } },
			// { path: 'views-courses', component: ViewsCoursesComponent, data: { title: 'Views' } },
			{ path: 'report', component: ReportComponent, data: { title: 'Peport' } },

			{ path: 'message', component: MessageComponent, data: { title: 'Message' } },
			{ path: 'search/:termino', component: SearchComponent, data: { title: 'Search' } },

			// ______________
			{ path: 'teacher/:id', component: AddUsersComponent, data: { title: 'Instructors' } },
			{ path: 'teachers', component: UsersComponent, data: { title: 'Instructors', sub: 'teacher' } },
			// ______________
			{ path: 'studen/:id', component: AddUsersComponent, data: { title: 'Student' } },
			{ path: 'studens', component: UsersComponent, data: { title: 'Students', sub: 'studen' } },
			{ path: '', redirectTo: 'dashboard', pathMatch: 'full' }
		]
	}
];


export const PAGES_ROUTES = RouterModule.forChild(pagesRoutes);
