import { Injectable, EventEmitter } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { URL_SERVICES } from 'src/app/config/config';
import { EnrolmentModel } from 'src/app/models/enrolment.model';
import { map, catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class EnrolmentService {
	public tipo: string;
	public id: string;
	public oculto = 'oculto';
	public 	eventUpdate = new EventEmitter<any>();
	constructor(
		public alert: ToastrService,
		public http: HttpClient,
		public router: Router,
	) { }
	add(enrlment: EnrolmentModel) {
		const url = `${URL_SERVICES}/Transactions`;
		return this.http.post(url, enrlment)
			.pipe(
				map((resp: any) => {
					this.alert.success('The student has been enrolled in the course', 'Excellent');
					return true;
				}),
				catchError((err: any) => {
					if (err.error.error == 'you_are_already_registered') {
						this.alert.error("can't add", 'you already have an assigned course');
					}
					console.log(err);
					return throwError(err);
				})
			);
	}
	list() {
		const url = `${URL_SERVICES}/Transactions`;
		return this.http.get(url);
	}
		ocultarModal() {
		this.tipo = '';
		this.id = '';
		this.oculto = 'oculto';
	}
	mostratModal() {
		// this.tipo = type;
		// this.id = id;
		this.oculto = '';
	}
}
