import { Component, OnInit } from '@angular/core';
// import { MenuService, EnrolmentService, UserService, NewEnrolmentService } from '../../services/service.index';
import { EnrolmentModel } from '../../models/enrolment.model';
import { MenuService, UsersService, EnrolmentService } from 'src/app/service/index.service';

@Component({
	selector: 'app-enrolment',
	templateUrl: './enrolment.component.html',
	styles: []
})
export class EnrolmentComponent implements OnInit {
	activeName = false;
	activeLessons = false;
	activeSections = false;
	activePeriod = true;
	activeStatus = true;
	activeTeacher = false;
	activeCategory = false;
	activePrice = false;
	activeEnrolments = false;
	activePublished = false;

	ColumnStatus = true;
	ColumnName = true;
	ColumnAuthor = true;
	ColumnCategory = true;
	ColumnPrice = true;
	ColumnLessons = true;
	ColumnSections = true;
	ColumnDemography = true;
	ColumnEnrolments = true;
	ColumnPublished = true;
	enroment: EnrolmentModel[] = [];
	loading = true;
	desde = 1;
	totalRegister: number;
	nextPege: number;
	next_page: number;
	prev_page: number;
	current_page: number;
	total_pages: number;
	arrayFilter = {name: '', lastname: '', verified: ''};
	constructor(public menu: MenuService, public _modal: EnrolmentService, public _serviceEnrolment: UsersService) { }

	ngOnInit() {
		this.listEneolment();
		this._modal.eventUpdate.subscribe((resp:any) => {
			this.listEneolment();
		})
	}
	GenerarAchivo(type) {

	}
	updateFilter(event, type) {
		const val = event.toLowerCase();

		if (type == 'name') {
			this.arrayFilter.name = val;
		}
		if (type == 'status') {
			this.arrayFilter.verified = val;
		}
		this.listEneolment();
	}
	listEneolment() {
		this.loading = true;
		this._serviceEnrolment.allStudents(this.desde, this.arrayFilter).subscribe((resp:any) => {	
			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page;
			this.total_pages = resp.last_page;
			this.loading = false;
			this.enroment = resp.data;
			console.log(resp.data);
		})
	}
	funtionDesde(value) {

		if(this.desde < 1) {
			this.desde = 1;
		}
		this.desde += value;
		this.listEneolment();
	}
}
