import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SubCategoryService, CategoryService } from 'src/app/service/index.service';
import { SubCategoryModel } from 'src/app/models/subCategory.model';
import { ToastrService } from 'ngx-toastr';

@Component({
	selector: 'app-sub-category',
	templateUrl: './sub-category.component.html',
	styles: []
})
export class SubCategoryComponent implements OnInit {
	subCategory: any = {};
	array = { name: '', subCategory: '' };
	forma: FormGroup;
	category: any = [];

	constructor(
		public _modalSubCategory: SubCategoryService,
		public _categoryService: CategoryService,
		public alert: ToastrService
		) {
		_modalSubCategory.notificacion.subscribe((resp: any) => {
			this.forma.setValue({
				category: resp.categories.id,
				name: resp.name
			});
		});
		_categoryService.categoryEvent.subscribe(resp => {
			// console.log(resp);
		
			if(resp) {
				_categoryService.listCategory(this.array).subscribe((resp: any) => {
					this.category = resp.data;
				});
			}

		});
		_modalSubCategory.oneSubCategory.subscribe(resp => {
			this.subCategory = resp;
		});
	}


	ngOnInit() {
		console.log(this._modalSubCategory.Deleteoculto);
		this.forma = new FormGroup({
			category: new FormControl(null, Validators.required),
			name: new FormControl(null, Validators.required),
		});
	}
	addSubCategory() {
		const Categoryedit = new SubCategoryModel(
			this.forma.value.name,
			'',
			this.forma.value.category
			);
		if (this._modalSubCategory.id == 'nuevo') {
			this._modalSubCategory.add(Categoryedit).subscribe((resp: any) => {
				this.alert.success('The subcategory was created', 'Successfully');
				this._modalSubCategory.ocultarModal();
				this.forma.value.name = '';
				this.forma.value.category = '';
				this._modalSubCategory.newSubCategory.emit(resp.data);
			});
		} else {
			this._modalSubCategory.edit(Categoryedit).subscribe((resp: any) => {
				this._modalSubCategory.eventEdit.emit(resp.data);
				this._modalSubCategory.ocultarModal();
				this.alert.success('It has updated', 'the subcategory');
			});
		}

	}
	deleteSubCategory() {
		this._modalSubCategory.destroid(this.subCategory.id).subscribe(resp => {
			this._modalSubCategory.oneSubCategory1.emit(resp);
			this.alert.success('Subcategory Was removed', 'successfully');
			this._modalSubCategory.ocultarModal();
		});
	}
}
