import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { debounceTime } from 'rxjs/operators';
import { UsersService, LockScreenService } from 'src/app/service/index.service';

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styles: []
})
export class HeaderComponent implements OnInit {
	search = new FormControl('');
	profile = '';
	constructor(public _serviceUser: UsersService, private _modalService: LockScreenService) { 
		if(_serviceUser.UserData.role ==  "admin") {
			this.profile =  "profile";
		}
		if(_serviceUser.UserData.role ==  "teacher") {
			this.profile =  "profile_";
		}
	 }
	ngOnInit() {
		this.search.valueChanges.pipe(debounceTime(300)).subscribe((resp: string) => {
			this.searchEmiter.emit(resp);
		});

	}
	@Output('search') searchEmiter = new EventEmitter<string>();
}
