import { RouterModule, Routes } from '@angular/router';
import { PagesExpertsComponent } from './init/pages-experts.component';
import { TeacherGuard } from '../Guard/teacher.guard';
import { IndexComponent } from './index/index.component';
import { CourseListComponent } from './courses/course-list/course-list.component';
import { ViewCoursesComponent } from './view-courses/view-courses.component';
import { ReproductorComponent } from './reproductor/reproductor.component';
import { SearchExpressComponent } from './search-express/search-express.component';
import { MessageExpertsComponent } from './message-experts/message-experts.component';
import { ProfileExpertsComponent } from './profile-experts/profile-experts.component';


const pagesRoutes: Routes = [
	{
		path: '', component: PagesExpertsComponent,
		canActivate: [TeacherGuard],
		children: [
			{ path: 'dashboard_', component: IndexComponent, data: { title: 'Dashboard' } },
			{ path: 'course_', component: CourseListComponent, data: { title: 'Dashboard' } },
			{ path: 'messages_', component: MessageExpertsComponent, data: { title: 'Dashboard' } },
			{ path: 'profile_', component: ProfileExpertsComponent, data: { title: 'Dashboard' } },
			{ path: 'viewsCourse/:id', component: ViewCoursesComponent, data: { title: 'Dashboard' } },
			{ path: 'reproduc', component: ReproductorComponent, data: { title: 'Dashboard' } },
			{ path: 'searchCourse', component: SearchExpressComponent, data: { title: 'Dashboard' } },
			{ path: '', redirectTo: 'dashboard_', pathMatch: 'full' }
		]
	}
];


export const PAGES_EXPERTS_ROUTES = RouterModule.forChild(pagesRoutes);
