import { Component, OnInit, Inject } from '@angular/core';
import { UserModel } from 'src/app/models/user.model';
import { MenuService, UsersService, UploadService } from 'src/app/service/index.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { DOCUMENT } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';

@Component({
	selector: 'app-add-users',
	templateUrl: './add-users.component.html',
	styles: []
})
export class AddUsersComponent implements OnInit {
	validateForm: any;
	dataRouter: any = [];
	formaUsers: FormGroup;
	userID: any;
	routerLinkss: any;
	imagenUser = 'null';
	users: UserModel;
	email: string;
	ubicate: { City: '', latitud: '', longitud: '', ipAddress: '' };
	constructor(
		@Inject(DOCUMENT) private _document,
		public menu: MenuService,
		public _userService: UsersService,
		public activateRouter: ActivatedRoute,
		public _upload: UploadService,
		public router: Router,
		public alert: ToastrService
	) {
		_upload.resetVariable();
		this.menu.getDataRouter().subscribe((resp: any) => {
			this.dataRouter = resp;
			if (resp.title == 'Instructors') {
				this.routerLinkss = 'teachers';
			} else {
				this.routerLinkss = 'studens';
			}
		});
		this.activateRouter.params.subscribe((param: any) => {
			if (param.id !== 'nuevo') {
				this.userID = param.id;
				this.validateForm = true;

				this._userService.showOne(param.id).subscribe((resp: any) => {
					this.users = resp.data;
					this.email = resp.data.email;
					this.imagenUser = resp.data.img;
					this.formaUsers.setValue({
						name: resp.data.name,
						lastname: resp.data.lastname,
						Biography: resp.data.Biography,
						email: resp.data.email,
						facebook_link: resp.data.Facebook,
						twitter_link: resp.data.Twitter,
						linkedin_link: resp.data.Linkedin,
						paypal_client_id: resp.data.Paypal_client_id,
						paypal_secret_key: resp.data.Paypal_secret_key,
						stripe_public_key: resp.data.Stripe_public_key,
						stripe_secret_key: resp.data.Stripe_secret_key,
						Company: resp.data.Company,
						Address: resp.data.Address,
						City: resp.data.City,
						Country: resp.data.Country,
						Postal_Code: resp.data.Postal_Code,
						telefono: resp.data.telefono,

					});
				});
			} else {
				this.validateForm = false;
			}

		});


	}

	ngOnInit() {
		console.log(this._userService.IPinfo);

		this.formaUsers = new FormGroup({
			name: new FormControl('', Validators.required),
			lastname: new FormControl('', Validators.required),
			Biography: new FormControl('', Validators.required),
			email: new FormControl({ value: '', disabled: this.validateForm }, [Validators.required, Validators.email]),
			facebook_link: new FormControl('', Validators.required),
			twitter_link: new FormControl('', Validators.required),
			linkedin_link: new FormControl('', Validators.required),
			paypal_client_id: new FormControl('', Validators.required),
			paypal_secret_key: new FormControl('', Validators.required),
			stripe_public_key: new FormControl('', Validators.required),
			stripe_secret_key: new FormControl('', Validators.required),



			Company: new FormControl('', Validators.required),
			Address: new FormControl('', Validators.required),
			City: new FormControl(this._userService.IPinfo.city, Validators.required),
			Country: new FormControl(this._userService.IPinfo.country, Validators.required),
			Postal_Code: new FormControl('', Validators.required),
			telefono: new FormControl('', Validators.required),

		});
	}

	createNewUser(Type) {
		console.log(this.formaUsers.value);


		let role;
		if (Type == 'Instructors') {
			role = 'teacher';
		} else {
			role = 'student';

		}

		let users = new UserModel(
			this.formaUsers.value.name,
			this.formaUsers.value.lastname,
			this.formaUsers.value.email,
			'password_temp',
			'password_temp',
			null,
			this.formaUsers.value.Biography,
			this.formaUsers.value.facebook_link,
			this.formaUsers.value.twitter_link,
			this.formaUsers.value.linkedin_link,
			this.formaUsers.value.paypal_client_id,
			this.formaUsers.value.paypal_secret_key,
			this.formaUsers.value.stripe_public_key,
			this.formaUsers.value.stripe_secret_key,
			null,
			role,
			null,
			null,
			this.formaUsers.value.Company,
			this.formaUsers.value.Address,
			this.formaUsers.value.City,
			this.formaUsers.value.Country,
			this.formaUsers.value.Postal_Code,
			this._userService.IPinfo.lat,
			this._userService.IPinfo.lon,
			this.formaUsers.value.telefono,
			this._userService.IPinfo.query
		);
		if (this.formaUsers.value.name.length < 3) {
			this.alert.info('Is a required field', 'Name');
			this.removeClass();
			this.tab_panelValitors('Basic_info_up', 'Basic_info_down', 'first_name');
			return;
		}
		if (this.formaUsers.value.lastname.length < 3) {
			this.alert.info('Is a required field', 'Last Name');
			this.removeClass();
			this.tab_panelValitors('Basic_info_up', 'Basic_info_down', 'last_name');
			return;
		}
		let email = this.formaUsers.value.email || this.email;
		let validate = this._userService.validarEmail(email);
		if (!validate) {
			this.alert.info('It is an invalid email', 'correct the mail');
			this.removeClass();
			this.tab_panelValitors('Login_credentials_up', 'Login_credentials_down', 'email_Credencialess');
			return;
		}
		if (email < 5) {
			this.alert.info('Is a required field', 'Email');
			this.removeClass();
			this.tab_panelValitors('Login_credentials_up', 'Login_credentials_down', 'email_Credencialess');
			return;
		}
		if (this.validateForm) {
			return this.editUser(users);
		} {
			return this.addUser(users);
		}
	}
	addUser(users) {
		this._userService.addUsers(users, this.dataRouter.title).subscribe(((resp: any) => {
			if (this.dataRouter.title == 'Instructors') {
				this.router.navigate(['teachers']);
			} else {
				this.router.navigate(['studens']);
			}
		}));
	}
	editUser(users) {
		this._userService.update(users, this.userID).subscribe(resp => {
			if (this.dataRouter.title == 'Instructors') {
				this.router.navigate(['teachers']);
			} else {
				this.router.navigate(['studens']);
			}
		});

	}

	nextUser(a_id_up, div_aria_donw, delete_up, delete_donw) {
		this._document.getElementById(delete_donw).setAttribute('class', 'tab-pane');
		this._document.getElementById(delete_up).setAttribute('class', 'nav-link');

		this._document.getElementById(a_id_up).setAttribute('class', 'nav-link active');
		this._document.getElementById(div_aria_donw).setAttribute('class', 'tab-pane fade show active');
	}


	removeClass() {
		this._document.getElementById('finish_down').setAttribute('class', 'tab-pane fade');
		this._document.getElementById('finish_up').setAttribute('class', 'nav-link');
	}

	tab_panelValitors(upModal: string, donwModal: string, idInput: string) {
		this._document.getElementById(upModal).setAttribute('class', 'nav-link active');
		this._document.getElementById(donwModal).setAttribute('class', 'tab-pane fade show active');
		this._document.getElementById(idInput).focus();
	}
}
