import {
  Injectable
} from '@angular/core';
import {
  HttpClient
} from '@angular/common/http';
import {
  URL_SERVICES
} from 'src/app/config/config';
import {
  UserModel
} from '../../models/user.model';
import {
  Observable,
  throwError
} from 'rxjs';
import {
  catchError,
  map
} from 'rxjs/operators';
import {
  Router
} from '@angular/router';
import {
  ToastrService
} from 'ngx-toastr';
import {
  MenuService
} from '../menu/menu.service';
import {
  IPinfo
} from 'src/app/models/infoIP.model';
// import {UploadService} from '../index.service'

@Injectable({
  providedIn: 'root'
})
export class UsersService {
  UserData: UserModel;
  token: any = '';
  IPinfo: IPinfo;
  statusLogin = false;
  constructor(
    public http: HttpClient,
    public router: Router,
    public alert: ToastrService,
    private _menu: MenuService
  ) {
    this.cargarStorage();

  }

  estalogiado() {
    let status: boolean;
    // tslint:disable-next-line: triple-equals

    return (this.token.length > 5) ? true : false;
  }

  cargarStorage() {
    if (localStorage.getItem('token')) {
      this.token = localStorage.getItem('token');
      this.UserData = JSON.parse(localStorage.getItem('user'));
      this.IPinfo = JSON.parse(localStorage.getItem('IPinfo'));
    } else {
      this.token = '';
      this.UserData = null;
    }
  }

  guardarStorage(user: UserModel, token: string) {
    localStorage.setItem('user', JSON.stringify(user));
    localStorage.setItem('token', token);
    this.UserData = user;
    this.token = token;

  }

  addUsers(user: UserModel, type = null): Observable < any > {
    const url = `${URL_SERVICES}/Users`;
    return this.http.post(url, user).pipe(
      catchError((e: any) => {
        if (type == 'Instructors') {
          this.alert.success('saved instructor', 'no mail was sent');
          this.router.navigate(['teachers']);
        } else {
          this.alert.success('saved Student', 'no mail was sent');

          this.router.navigate(['studens']);
        }
        // console.log(e);
        return throwError(e);
      }),

    );

  }

  initSession(user: UserModel, recuerdame: boolean = false): Observable < any > {

    if (recuerdame) {
      localStorage.setItem('email', user.email);
    } else {
      localStorage.removeItem('email');
    }
    this._menu.getIpUser().subscribe((resp: IPinfo) => {
      console.log(resp);

      localStorage.setItem('IPinfo', JSON.stringify(resp));
    });
    const url = `${URL_SERVICES}/login`;
    return this.http.post(url, user).pipe(
      catchError(e => {
        console.log('Error initSessio', e);
        return throwError(e);
      }),
      map((resp: any) => {
        // tslint:disable-next-line: triple-equals
        if (resp.ok == 'true') {
          this.statusLogin = true;
          this.guardarStorage(resp.data, resp.token);
          return true;
        }
        return false;
      })

    );
  }

  logout() {
    this.token = '';
    this.UserData = null;
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('IPinfo');
    window.location.href = '/';


  }

  resetPassword(email) {
    const url = `${URL_SERVICES}/users/verifyEmail/${email}`;
    return this.http.get(url).pipe(
      catchError(e => {
        console.log('Error Reset mmessage', e);
        return throwError(e);
      }),

    );

  }

  newPassword(token, User: UserModel) {
    const url = `${URL_SERVICES}/users/resetPassword/${token}`;
    return this.http.post(url, User).pipe(
      catchError(err => {
        console.log('new password', err);
        this.alert.error('login Error', 'Error!');
        return throwError(err);
      }),
      map(resp => {
        this.alert.success('Open your email and reset your password', 'Excellent!');
        // swal('Excellent!', '', 'success');
        this.router.navigate(['login']);
      })
    );
  }
  allStudents(desde: number = 1, filter: any) {
    const url = `${URL_SERVICES}/Students?page=${desde}`;
    return this.http.get(url, {
      params: filter
    }).pipe(
      map((resp: any) => {
        return resp.data;
      })
    );
  }
  allTeacher(desde: number = 1, filter: any) {
    const url = `${URL_SERVICES}/instructor?page=${desde}`;
    return this.http.get(url, {
      params: filter
    }).pipe(
      map((resp: any) => {
        return resp.data;
      })
    );
  }
  showOneStudent(id) {
    const url = `${URL_SERVICES}/Students/${id}`;
    return this.http.get(url).pipe(
      catchError(err => {
        console.log('showOneStudent', err);
        return throwError(err);
      })
    );
  }
  showOneTeacher(id) {
    const url = `${URL_SERVICES}/Teachers/${id}`;
    return this.http.get(url).pipe(
      catchError(err => {
        console.log(err);
        return throwError(err);
      })
    );
  }
  cargarUsuarios() {
    const url = `${URL_SERVICES}/Users`;
    return this.http.get(url).pipe(
      catchError(err => {
        if (err.statusText == 'Unknown Error') {
          this.logout();
          window.location.href = '/';
        }

        return throwError(err);
      })
    );
  }
  showOne(id) {
    const url = `${URL_SERVICES}/Users/${id}`;
    return this.http.get(url, id);
  }
  update(User: UserModel, id) {
    const url = `${URL_SERVICES}/Users/${id}`;
    return this.http.put(url, User).pipe(
      catchError(err => {
        console.log(err);
        this.alert.info('There were no changes', 'Attention');
        return throwError(err);
      }),
      map(resp => {
        this.alert.success('the user was updated', 'Excellent');
        return resp;
      })
    );
  }
  Destroy(id) {
    const url = `${URL_SERVICES}/Users/${id}`;
    return this.http.delete(url).pipe(
      catchError(err => {
        console.log(err);
        return throwError(err);
      })
    );
  }
  validarEmail(valor) {
    const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;
    if (emailRegex.test(valor)) {
      return true;
    } else {
      return false;
    }
  }
  searchAll(parant) {
    const url = `${URL_SERVICES}/search/${parant}`;
    return this.http.get(url).pipe(
      catchError(err => {
        this.alert.error('err-search', 'err');
        return throwError(err);
      })
    );
  }
  showAllStudent() {
    const url = `${URL_SERVICES}/showStudent`;
    return this.http.get(url).pipe(
      catchError(err => {
        this.alert.error('Esto no sirve', 'err');
        return throwError(err);
      })
    );
  }
  // ______________expers______________

  showCourseExperts() {
    const url = `${URL_SERVICES}/Teachers/${this.UserData.id}`;
    return this.http.get(url).pipe(
      catchError((err: any) => {
        console.log('Error show Course Express', err);
        return throwError(err);
      })
    );
  }
}
