import {
  Injectable,
  EventEmitter,
  Output
} from '@angular/core';
import {
  MenuService
} from '../../menu/menu.service';
import {
  UsersService
} from '../../users/users.service';
import {
  CoursesService
} from '../../courses/courses.service';
import {
  Router
} from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class ViewsCoursesService {
  oculto = 'oculto'
  off = true;
  saveParams: string;
  public active: any = {
	name: false,
	editName: false,
	Description: false,
	Discounted_price: false,
	Language: false,
	Level: false,
	Meta_description: false,
	Short_description: false,
	course_discount: false,
	course_seo: false,
	free_course: 0,
	img: true,
	is_top_course: false,
	outcome: false,
	requirements: false,
	status: false,

  }
  @Output() showCategory = new EventEmitter < any > ();
  constructor(
	private modelCourse: MenuService,
	private _userModel: UsersService,
	private _courseService: CoursesService,
	private router: Router

  ) {}
  cerraModal() {
	this.oculto = 'oculto';
  }

  public subName = {
	outcome : '',
	requirements: '',
	leccion: '',
	section: ''
  }
  mostrarModal(data = 'name') {
	this.resetElement();
	this.saveParams = data;

	if (data == 'name') {
		this.showCategory.emit(true);
	 this.active.name = true;
	}
	if (data == 'editName') {
	  this.active.editName = true;
	}
	if (data == 'outcome') {
	  this.active.outcome = true;
	}
	if (data == 'requirements') {
		this.active.requirements = true;
	}

	this.oculto = '';
  }
  saveCourse(value: number) {
	let course = {
	  Course_title: this.modelCourse.modelCourse.Course_title,
	  Category: value,
	  Description: this.modelCourse.modelCourse.Description,
	  teacher_id: this._userModel.UserData.id,
	  Course_price: 0,
	  Short_description: 'edit description'
	}
	this._courseService.addCourse(course).subscribe((resp: any) => {
	  this.router.navigate(['viewsCourse', resp.data.id]);
	  this.cerraModal();
	})

  }

  resetElement() {
	// this.active.name = false;
	this.active.editName = false;
	this.active.name = false;
	this.active.editName = false;
	this.active.Description = false;
	this.active.Discounted_price = false;
	this.active.Language = false;
	this.active.Level = false;
	this.active.Meta_description = false;
	this.active.Short_description = false;
	this.active.course_discount = false;
	this.active.course_seo = false;
	this.active.free_course = false;
	this.active.img = true;
	this.active.is_top_course = false;
	this.active.outcome = false;
	this.active.requirements = false;
	this.active.status = false;

  }
 
}
