import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Category } from '../../models/category.model';
import { ToastrService } from 'ngx-toastr';
import { CategoryService, UploadService } from 'src/app/service/index.service';
@Component({
	selector: 'app-add-catgory',
	templateUrl: './add-catgory.component.html',
	styles: []
})
export class AddCatgoryComponent implements OnInit {
	idCategory: any;
	add = true;
	forma: FormGroup;
	fallbackIcon = 'fas fa-user';
	icon: string;
	event = document.createEvent('UIEvents');
	constructor(
		public router: Router,
		public _categoryServices: CategoryService,
		public _uploadService: UploadService,
		public activateRouter: ActivatedRoute,
		public alert: ToastrService
	) {
		_uploadService.resetVariable();

		this.activateRouter.params.subscribe((params: any) => {
			const id = params['id'];
			this.idCategory = params['id'];
			if (id !== 'nuevo') {
				this.add = false;
				this._categoryServices.showOne(id).subscribe((resp: any) => {
					this.icon = resp.data.iconpiker;
					this.fallbackIcon = resp.data.iconpiker;
					this.forma.setValue({
						codeCategory: resp.data.codecategory,
						titleCategory: resp.data.name,
						descriptionCategory: resp.data.description,
						icone: resp.data.iconpiker
					});
				});
			}

		});




	}
	ngOnInit() {
		this.forma = new FormGroup({
			codeCategory: new FormControl(null, Validators.required),
			titleCategory: new FormControl(null, Validators.required),
			descriptionCategory: new FormControl(this.icon, Validators.required),
			icone: new FormControl(null, Validators.required)
		});

		//

	}

	addCategory() {

		this.forma.value.icone = this.icon;
		let category = new Category(
			this.forma.value.titleCategory,
			this.forma.value.descriptionCategory,
			this.forma.value.codeCategory,
			this.forma.value.icone,
		);
		if (this.add) {
			this.addDBCategory(category);
			return;
		}
		this.editCategory(category);

	}
	onIconPickerSelect(icon: string): void {
		this.icon = icon;
	}


	addDBCategory(category) {
		this._categoryServices.addCategoty(category)
			.subscribe((resp: any) => {
				if (this._uploadService.imagenSubir != null) {

					this._uploadService.sublirArchivo('category', resp.data.id).then(resp => {
						this._categoryServices.eventUpdateCategory.emit(resp);

					}).catch(err => this.alert.error('error loading image', 'verify what happened on the server'));
				}
				// swal('exito', 'se guardo la categoria', 'success');
				this.alert.success('Success', 'The category was saved');
				this.router.navigate(['/category']);
			});
	}
	editCategory(category) {
		this._categoryServices.editCategory(this.idCategory, category).subscribe(resp => {
			if (this._uploadService.imagenSubir != null) {

				this._uploadService.sublirArchivo('category', this.idCategory).then(resp => {
				}).catch(err => this.alert.error('The image could not be loaded'));
			}
			this.alert.success('Success', 'The category was saved');
			this.router.navigate(['/category']);

		});
	}

}
