import { Injectable } from '@angular/core';
import { URL_SERVICES } from 'src/app/config/config';
import { ToastrService } from 'ngx-toastr';

@Injectable({
	providedIn: 'root'
})
export class UploadService {
	nameimage: string = 'Select Image';
	imagenSubir: File;
	imagenTemporal: string | ArrayBuffer;

	constructor(public alert: ToastrService) { }

	sublirArchivo(tipeArchive, id) {
		return new Promise((resolve, reject) => {
			let formData = new FormData();
			let xhr = new XMLHttpRequest();
			formData.append('imagen', this.imagenSubir, this.imagenSubir.name);
			xhr.onreadystatechange = () => {
				if (xhr.readyState === 4) {
					if (xhr.status == 200) {
						this.alert.success('I load the image');
						resolve(JSON.parse(xhr.response));

					} else {

						this.alert.error('Imagen', 'Server error');
						reject(xhr.response);
					}
				}
			};

			let url = `${URL_SERVICES}/upload/${tipeArchive}/${id}`;
			xhr.open('POST', url, true);
			xhr.send(formData);
		});
	}
	selectImg(event: File) {
		this.nameimage = event.name;
		if (!event) {
			this.imagenSubir = null;
			return;
		}

		if (event.size > 2097538) {

			this.alert.success('Is very heavy', 'The files have to be less than 2MB');
			this.nameimage = 'Imagen no Validad';

			return;
		}
		this.imagenSubir = event;
		const reader = new FileReader();
		const urlImagentemp = reader.readAsDataURL(event);

		reader.onloadend = () => this.imagenTemporal = reader.result;
	}
	resetVariable() {
		this.nameimage = 'Select Image';
		this.imagenSubir = null;
		this.imagenTemporal = '';
	}
}
