import { Component, OnInit } from '@angular/core';
import { CoursesService } from 'src/app/service/index.service';
import { Courses } from 'src/app/models/course.model';

@Component({
  selector: 'app-search-express',
  templateUrl: './search-express.component.html',
  styles: []
})
export class SearchExpressComponent implements OnInit {
  resolveData = true;
  constructor(private _courseService: CoursesService) { }
  course: Courses[] = [];
  ngOnInit() {
    this.listCourses();
    
  }
  listCourses() {
    console.log('hola__');
    
    this.resolveData = true;
    this._courseService.showAll().subscribe((resp: any) => {
      this.course = resp.data;
    this.resolveData = false;
      
    })
  }
}
