
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { catchError, map } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { URL_SERVICES } from 'src/app/config/config';
import { Courses } from 'src/app/models/course.model';

@Injectable({
	providedIn: 'root'
})

export class CoursesService {

	constructor(public http: HttpClient, public router: Router, public alet: ToastrService) { }

	addCourse(course) {


		const url = `${URL_SERVICES}/cursos`;
		return this.http.post(url, course).pipe(
			catchError((err: any) => {
				if (err.status < 500) {
					if (err.error.error.Course_title) {
						this.alet.info('', err.error.error.Course_title[0]);
					}

					if (err.error.error) {

					}
				}

				return throwError(err);

			})
		);
	}

	listCourses(desde: number = 1, filter: any) {

		const url = `${URL_SERVICES}/showCourses?page=${desde}`;
		return this.http.get(url, { params: filter }).pipe(
			map((resp: any) => {

				resp.data.path = null;
				return resp.data;
			}), catchError((err: any) => {
				console.log(err);

				return throwError(err);

			})
		);
	}

	deleteCourse(id) {
		const url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.delete(url).pipe(
			catchError((err) => {
				console.log('deleteCourse ', err);

				return throwError(err);
			})
		);

	}
	showOne(id) {
		const url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.get(url);
	}
	editCourses(id, course: Courses) {
		const url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.put(url, course).pipe(
			catchError(err => {
				console.log(err);
				
				return throwError(err);
			})
		);
	}
	showAll() {
		const url = `${URL_SERVICES}/cursos`;
		return this.http.get(url).pipe(
			catchError((err: any) => {
				console.log('Show All', err);
				return throwError(err);
			})
		);
	}
	showPDF() {
		const url = `${URL_SERVICES}/download_pdf`;
		window.location.href = url;
	}
	showExcel() {
		const url = `${URL_SERVICES}/download_excel`;
		window.location.href = url;
	}

	getMonth() {
		const date = new Date();
		const mes = date.getMonth() + 1;
		const url = `${URL_SERVICES}/graphics/${mes}`;
		return this.http.get(url).pipe(
			catchError((e: any) => {
				console.log('getMonth', e);
				return throwError(e);
			}),
			map((resp: any) => {
				return resp.data;
			})
		);
	}
	getCourseYear() {
		const url = `${URL_SERVICES}/graphics`;
		return this.http.get(url).pipe(
			catchError(e => {
				return throwError(e);
			})
		);
	}
}
