import { Component, OnInit, Input } from '@angular/core';
import { CoursesService } from 'src/app/service/index.service';
@Component({
	selector: 'app-grafi-details',
	templateUrl: './grafi-details.component.html',
	styles: []
})
export class GrafiDetailsComponent implements OnInit {
	@Input() chart: ApexChart;
	options1: any;
	options2: any;
	constructor(public _corseService: CoursesService) { }

	ngOnInit() {
		const ff = [];
		this.cargarGraficas();
		this._corseService.getCourseYear().subscribe((resp: any) => {
			return this.cargarGraficas(resp.data);
		});



	}
	cargarGraficas(value = []) {
		this.options1 = {
			chart: { height: 319, offsetY: -10, type: 'bar', stacked: !0, toolbar: { show: !1 }, zoom: { enabled: !0 } },
			plotOptions: {
				startAngle: -135,
				bar: {
					horizontal: true,
				}
			},
			dataLabels: {
				enabled: true
			},
			series: [
				{ name: 'Rejected', data: value },
				{ name: 'Pending', data: [13, 23, 20, 8, 13, 27, 18, 22, 10, 16, 24, 22] },
				{ name: 'Approved', data: [11, 17, 15, 15, 21, 14, 11, 18, 17, 12, 20, 18] },
			],
			xaxis: { categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'] },
			colors: ['#8b0013', '#f1b44c', '#34c38f'],
			legend: { position: 'bottom' },

		};
		this.options2 = {
			chart: { height: 200, type: 'radialBar', offsetY: -10 },
			plotOptions: {
				radialBar: {
					startAngle: -135,
					endAngle: 135,
					dataLabels: {
						name: { fontSize: '13px', color: void 0, offsetY: 60 },
						value: {
							offsetY: 22,
							fontSize: '16px',
							color: void 0,
							formatter (e) {
								return e + '%';
							},
						},
					},
				},
			},
			colors: ['#8b0013'],
			// tslint:disable-next-line: max-line-length
			fill: { type: 'gradient', gradient: { shade: 'dark', shadeIntensity: 0.15, inverseColors: !1, opacityFrom: 1, opacityTo: 1, stops: [0, 50, 65, 91] } },
			stroke: { dashArray: 4 },
			series: [67],
			labels: ['Rejected'],
		};
	}

}
