import { Component, OnInit } from '@angular/core';

import { Courses } from '../../models/course.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { EnrolmentModel } from '../../models/enrolment.model';
import { EnrolmentService, UsersService, CoursesService } from 'src/app/service/index.service';
import { UserModel } from 'src/app/models/user.model';

@Component({
	// tslint:disable-next-line: component-selector
	selector: 'modal-enrolment',
	templateUrl: './enrolment.component.html',
	styles: []
})
export class EnrolmentComponent implements OnInit {
	forma: FormGroup;
	_Courses: Courses;
	studen: UserModel;
	constructor(
		public _modal: EnrolmentService,
		public _studenSevice: UsersService,
		public _courseService: CoursesService,
		public _serviceEnrolment: EnrolmentService
	) { }

	ngOnInit() {
		this.forma = new FormGroup({
			Student_id: new FormControl(null, Validators.required),
			course_id: new FormControl(null, Validators.required),
		});
		this.listCourses();
		this.listStuden();
	}
	ocultarModal() {
		this._modal.ocultarModal();
	}
	listStuden() {
		this._studenSevice.showAllStudent().subscribe((resp: any) => this.studen = resp.data);
	}
	listCourses() {
		this._courseService.showAll().subscribe((resp: any) => this._Courses = resp.data);
	}
	addEnrolment() {
		if (this.forma.invalid) {
			return;
		}
		const inscription = new EnrolmentModel(this.forma.value.Student_id, this.forma.value.course_id);
		this._serviceEnrolment.add(inscription).subscribe(resp => {
			if (resp) {
				this.ocultarModal();
				this._modal.eventUpdate.emit(resp);
			}
		});
	}
	// (click)="subirImgen()" [disabled]="!imagenTemporal"
}
