import { Component, OnInit } from '@angular/core';
import { UsersService, MenuService } from 'src/app/service/index.service';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({

	selector: 'app-reset-password',
	templateUrl: './reset-password.component.html',
	styles: []
})
export class ResetPasswordComponent implements OnInit {
	event = document.createEvent('UIEvents');
	emailValidate: string;
	email: string;
	buttonReset = true;
	constructor(
		// tslint:disable-next-line: variable-name
		private _userService: UsersService,
		private alert: ToastrService,
		private router: Router,
		private _menu: MenuService
		) { }

	ngOnInit() {
		this._menu.removePreload('login');
	}
	resetPasswod(forma: NgForm) {
		if (forma.invalid) {
			return;
		}
		const email = forma.value.txtEmail;
		if (!this._userService.validarEmail(email)) {

			return;
		}
		this.buttonReset = false;
		this._userService.resetPassword(email).subscribe((resp: any) => {

			// swal('Attention!', 'To reset your password you must open the email and update it', 'info');
			this.alert.info('Attention!', 'To reset your password you must open the email and update it');


			this.router.navigate(['ok', email]);
		});
	}
}
